
import { Link } from "react-router-dom";

import React , {useState} from 'react'
import "./style.css"

function SignUp() {
    const [email, setemail] = useState("")
    const [firstname, setfirstname] = useState("")
    const [lastname, setlastname]= useState("")
    const [number, setnumber] = useState("")
    const [registered, setregistered] = useState("")
    const handleregister = (e) => {
        e.preventDefault()
        setregistered("Successfully registered check console ")
        console.log({email})
        console.log( {firstname} )
        console.log({lastname})
        console.log({number})
        
    }
    return (
        <form onSubmit={handleregister}>
                <h3>Sign Up</h3>
                <p>{registered} </p>

                <div className="form-group">
                    <label> First Name</label>
                    <input type="text" className="form-control" placeholder="First Name" value = {firstname}
                    onChange = {(e)=> {setfirstname(e.target.value)}}  />
                </div>
                <div className="form-group">
                    <label> Last Name</label>
                    <input type="text" className="form-control" placeholder="Last Name" value = {lastname}
                    onChange = {(e)=> {setlastname(e.target.value)}}  />
                </div>
                <div className="form-group">
                    <label>Email address</label>
                    <input type="email" className="form-control" placeholder="Enter email"  value = {email}
                    onChange = {(e)=> {setemail(e.target.value)}} />
                </div>

                <div className="form-group">
                    <label>Mobile Number</label>
                    <input type="number" className="form-control" placeholder="Enter Mobile Number"  value = {number}
                    onChange = {(e)=> {setnumber(e.target.value)}} />
                </div>

                <button type="submit" className="btn btn-primary btn-block">Sign Up</button>
                <p className="forgot-password text-right">
                    Already registered <Link to="/">Login</Link>
                </p>
            </form>
    )
}

export default SignUp
